import React, { useEffect, useRef, useState } from 'react'
import { Dropdown, DropdownButton, Form, InputGroup } from 'react-bootstrap';
import { ChromePicker } from 'react-color';
import "../components/upload-video/upload.css"
import { IoColorPaletteOutline } from "react-icons/io5";
import { IoClose } from "react-icons/io5";
import RangeSlider from 'react-range-slider-input';
import { FaPlay, FaSpinner } from "react-icons/fa";
import 'react-range-slider-input/dist/style.css';

import { MdAddShoppingCart } from "react-icons/md";
import { BiArrowBack, BiStar, BiTrash } from 'react-icons/bi';
import { BsFillStarFill } from 'react-icons/bs';
import axios from 'axios';
import { useSelector } from 'react-redux';
import { toast } from 'react-toastify';
import { FallingLines } from 'react-loader-spinner';
import countryCodes from '../utils/countryCodes';
import VideoPreviewModel from '../components/video-preview/VideoPreviewModel';
import SelecteProducts from '../components/upload/SelecteProducts';
import UplaodFile from '../components/upload/UplaodFIle';
import SelectPlayList from '../components/upload/SelectPlayList';
import { useNavigate, useLocation } from 'react-router-dom';


function getPhoneCodeByCountryCode(code) {
    const country = countryCodes.find(country => country.code === code);
    return country ? country.phoneCode : null;
}
function getCountryCodeByPhoneCode(phonecode) {
    let x;
    if (phonecode == "+1") {
        x = "US"
    } else if (phonecode == "+91") {
        x = "IN"
    } else if (phonecode == "+61") {
        x = "AU"
    }

    return x
}
const VideoEditPage = () => {
    const navigate = useNavigate()
    const [selectedTab, setSelectedTab] = useState('0');
    const [selectedCTATab, setSelectedCTATab] = useState('0');
    const [selectedItems, setSelectedItems] = useState([]);
    const [selectedCountry, setSelectedCountry] = useState('US')
    const [selectedCallNowType, setSelectedCallNowType] = useState('0')
    const [color, setColor] = useState('#000000');
    const [color2, setColor2] = useState('#ffa800');
    const [showColorPicker, setShowColorPicker] = useState(false);
    const [showColorPicker2, setShowColorPicker2] = useState(false);
    const [previewUrl, setPreviewUrl] = useState(null);
    const [videoFIle, setvideoFIle] = useState(null);
    const [previewModel, setPreviewModel] = useState(false)
    const colorPickerRef = useRef(null);
    const colorPickerRef2 = useRef(null);
    const [coverImageData, setCoverImageData] = useState(null);
    const [selectedPlalist, setselectedPlalist] = useState([]);
    const [loadingPercent, setLoadingPercent] = useState(0)
    const [imagePreview, setImagePreview] = useState(null)
    const user = useSelector(state => state.auth.user)
    const [playlists, setPlaylists] = useState([])
    const [products, setProducts] = useState([])
    const [videoSettings, setVideoSettings] = useState({})
    const [number, setNumber] = useState("")
    const [image, setImage] = useState(null)
    const [loading, setLoading] = useState(false)
    const [inputFields, setInputFields] = useState({
        videoTitle: '',
        ctaBtn: '',
        videURL: '',
        pageUrl: '',
        videoType: '',
        videLength: 0

    })
    const [fetchedVideoData, setFetchedVideoData] = useState({})

    const location = useLocation()
    const urlarray = location.pathname.split('/')
    const video_id = urlarray[urlarray.length - 1]
    const videoData = useSelector(state => state.auth.videoDataForEdit)

    // setPreviewUrl(videoData.)
    function extractFieldAndJoin(array, fieldName) {
        // Check if the array is not empty
        if (array.length === 0) {
            return ""; // Return an empty string if the array is empty
        }

        // Extract the specified field from each object
        const values = array.map(obj => obj[fieldName]);

        // Join the values into a comma-separated string
        const result = values.join(", ");

        return result;
    }
    const videoRef = useRef(null);
    const handleColorChange = (newColor) => {
        setColor(newColor.hex);
    };
    const clearPreview = () => {
        setPreviewUrl(null);
        setvideoFIle(null)
    };

    const handleClosePreviewModel = () => {
        setPreviewModel(false)
    }

    const handleClick = () => {
        setShowColorPicker(!showColorPicker);
    };
    const handleColorChange2 = (newColor) => {
        setColor2(newColor.hex);
    };
    const handleInputChangeAllFields = (e) => {
        const { name, value } = e.target;
        setInputFields(prevInputFields => ({
            ...prevInputFields,
            [name]: value,
        }));
    };

    const handleClick2 = () => {
        setShowColorPicker2(!showColorPicker2);
    };

    const handleTabChange = (event) => {
        setSelectedTab(event.target.value);
        setPreviewUrl(null)
        setvideoFIle(null)
    };
    const handleCTATabChange = (event) => {
        setSelectedCTATab(event.target.value);
    };
    const handleDropdownSelect = (value) => {
        setSelectedCountry(value);
    };
    useEffect(() => {
        const handleClickOutside = (event) => {
            if (colorPickerRef.current && !colorPickerRef.current.contains(event.target)) {
                setShowColorPicker(false);
            }
            if (colorPickerRef2.current && !colorPickerRef2.current.contains(event.target)) {
                setShowColorPicker2(false);
            }
        };

        document.addEventListener('mousedown', handleClickOutside);
        return () => {
            document.removeEventListener('mousedown', handleClickOutside);
        };
    }, []);
    const handleInputChange = (event) => {
        setvideoFIle(event.target.value);
        handleInputChangeAllFields(event)

    };

    // useEffect(() => {
    //     if (videoRef.current) {
    //         const videoElement = videoRef.current
    //         videoElement.addEventListener('loadedmetadata', () => {
    //             width = videoElement.videoWidth;
    //             height = videoElement.videoHeight;


    //         });
    //     }
    // }, [videoRef])

    const videoDimentionType = () => {
        const video = videoRef.current
        if (video) {
            video.addEventListener('loadedmetadata', () => {
                const width = video.videoWidth;
                const height = video.videoHeight;

                setInputFields(prevInputFields => ({
                    ...prevInputFields,
                    videLength: video.duration,
                }));
                if (height > width) {
                    setInputFields(prevInputFields => ({
                        ...prevInputFields,
                        videoType: "portrait",
                    }));
                } else if (width === height) {
                    setInputFields(prevInputFields => ({
                        ...prevInputFields,
                        videoType: "square",
                    }));
                } else {
                    setInputFields(prevInputFields => ({
                        ...prevInputFields,
                        videoType: "landscape",
                    }));
                }
            });
        }
    }

    // // Function to handle file selection
    // const handleFileSelect = event => {
    //     const file = event.target.files[0];
    //     setCoverImageData(file);

    // }

    const handleFileSelect = (e) => {
        const file = e.target.files[0];

        if (file) {
            setImage(file)
            const reader = new FileReader();
            reader.onloadend = () => {
                const imageData = reader.result;
                setImagePreview(imageData);
                // You can send the binary data (imageData) to your server here
            };
            reader.readAsDataURL(file); // Convert the file to a base64 encoded string
        }
    };
    const fileInputRef = useRef(null);

    const clearSelection = () => {
        setCoverImageData(null);
        setImagePreview(null)
        if (fileInputRef.current) {
            fileInputRef.current.value = ''; // Reset file input value
        }
    };
    const handleSubmitEdit = async (e) => {
        e.preventDefault();
        setLoading(true)

        try {
            // setLoadingPercent(0);
            const formData = new FormData();

            formData.append('image', image);

            formData.append('videoId', videoData.video_id);
            formData.append('uploadType', selectedTab);
            formData.append('selectedCallNowType', selectedCallNowType);
            formData.append('videURL', inputFields.videURL);
            formData.append('videoTitle', inputFields.videoTitle);
            formData.append('cta_type', selectedCTATab);
            formData.append('tollfree_or_phone', selectedCallNowType);
            formData.append('ctaButton', JSON.stringify({
                cta_btn_text: inputFields.ctaBtn,
                cta_bg_color: color,
                cta_text_color: color2
            }));

            formData.append('selectedPlylist', extractFieldAndJoin(selectedPlalist, "id"));
            formData.append('connected_products', extractFieldAndJoin(selectedItems, "productId"));
            // formData.append('cover_image', image);
            formData.append('videoType', inputFields.videoType);
            formData.append('video_duration', inputFields.videLength);
            formData.append('country', getPhoneCodeByCountryCode(selectedCountry));
            formData.append('number', number);
            // formData.append('videoId', res.data.key.split(".")[0]);
            // formData.append('uploadedVideoUrl', res.data.url.split('?')[0]);
            formData.append('brandId', user.brand_id);
            formData.append('productUrl', inputFields.pageUrl);
            await axios.patch("https://liveshopping.asia/updateVideoData", formData, {
                headers: {
                    'Content-Type': 'multipart/form-data'
                }
            }).then((res) => {

                setLoading(false)
                toast.success("Swirl updated successfully!")
            }).catch((err) => {
                console.log(err);
                setLoading(false)
                toast.error("Something went wrong !")
            })
        } catch (error) {
            console.log(error);
            toast.error("Something went wrong !")
        }
    }

    const handleSubmitUpload = async (e) => {
        e.preventDefault();
        setLoading(true)
        if (!previewUrl && !videoFIle) {
            console.error("No file selected for upload.");
            return;
        }

        if (videoFIle.includes("MP4") || videoFIle.includes("mp4") || videoFIle.includes("Mp4")) {
            try {
                const config = {
                    onUploadProgress: (progressEvent) => {
                        const percentCompleted = Math.round(
                            (progressEvent.loaded * 100) / progressEvent.total
                        );
                        // Update your UI with upload progress
                    },
                };
                await axios.post("https://liveshopping.asia/uploadVideoWithUrl", {
                    videoURL: videoFIle
                }, config).then((res) => {
                    if (res.data.status == 1) {
                        setLoading(false)
                        setPreviewUrl(null)
                        setselectedPlalist([])
                        setSelectedItems([])
                        setImage(null)
                        setImagePreview(null)
                        e.target.reset()
                        toast.success('Video uploaded successfully')
                    } else {
                        toast.error("Something went wrong!")
                        setLoading(false)
                    }
                }).catch((error) => {
                    console.log(error);
                    setLoading(false)
                    toast.error("Something went wrong!")
                })
            } catch (error) {
                console.log(error);
                setLoading(false)
                toast.error("Something went wrong!")
            }
        } else {
            try {
                const res = await axios.post("https://liveshopping.asia/getSignedUrl");
                if (res.data.url) {

                    await axios.put(res.data.url, previewUrl, {
                        headers: {
                            'Content-Type': previewUrl.type,
                        },
                        onUploadProgress: progressEvent => {
                            const percentage = Math.round((progressEvent.loaded * 100) / progressEvent.total);
                            setLoadingPercent(percentage);
                        }
                    }).then(async (response) => {
                        // setPreviewUrl(null);
                        // setvideoFIle(null)
                        // setLoadingPercent(0);
                        const formData = new FormData();

                        formData.append('image', image);
                        formData.append('uploadType', selectedTab);
                        formData.append('selectedCallNowType', selectedCallNowType);
                        formData.append('videURL', inputFields.videURL);
                        formData.append('videoTitle', inputFields.videoTitle);
                        formData.append('cta_type', selectedCTATab);
                        formData.append('tollfree_or_phone', selectedCallNowType);
                        formData.append('ctaButton', JSON.stringify({
                            cta_btn_text: inputFields.ctaBtn,
                            cta_bg_color: color,
                            cta_text_color: color2
                        }));

                        formData.append('selectedPlylist', extractFieldAndJoin(selectedPlalist, "id"));
                        formData.append('connected_products', extractFieldAndJoin(selectedItems, "productId"));
                        // formData.append('cover_image', image);
                        formData.append('videoType', inputFields.videoType);
                        formData.append('video_duration', inputFields.videLength);
                        formData.append('country', getPhoneCodeByCountryCode(selectedCountry));
                        formData.append('number', number);
                        formData.append('videoId', res.data.key.split(".")[0]);
                        formData.append('uploadedVideoUrl', res.data.url.split('?')[0]);
                        formData.append('brandId', user.brand_id);
                        formData.append('productUrl', inputFields.pageUrl);

                        await axios.post("https://liveshopping.asia/insertVideo", formData, {
                            headers: {
                                'Content-Type': 'multipart/form-data'
                            }
                        }).then((asdas) => {

                            setLoading(false)
                            setInputFields({
                                videoTitle: videoSettings.buy_btn,
                                ctaBtn: '',
                                videURL: '',
                                pageUrl: '',
                                videoType: '',
                                videLength: 0

                            })
                            setLoadingPercent(0)
                            setvideoFIle(null)
                            setPreviewUrl(null)
                            setselectedPlalist([])
                            setSelectedItems([])
                            setImage(null)
                            e.target.reset()
                            toast.success('Video uploaded successfully')

                        }).catch((error) => {
                            console.log(error);
                            setLoading(false)
                            toast.error("Something went wrong!")
                        })
                    }).catch((error) => {
                        console.log(error);
                        setLoading(false)
                        toast.error("Something went wrong!")
                    })
                }
            } catch (error) {
                console.error("Upload error:", error);
                toast.error("Something went wrong!")
            }
        }
    }


    const getPlalists = async () => {
        try {
            await axios.get(`https://liveshopping.asia/getPlaylistByBrandId?brandId=${user.brand_id}`).then((res) => {
                if (res.data.playlist) {
                    setPlaylists(res.data.playlist)
                } else {
                    setPlaylists([])
                }
            })
        } catch (error) {
            console.log(error);
        }
    }
    const getProducts = async () => {
        try {
            await axios.get(`https://liveshopping.asia/getProductsByBrandId?brandId=${user.brand_id}`).then((res) => {
                if (res.data.products) {
                    setProducts(res.data.products)
                } else {
                    setProducts([])
                }
            })
        } catch (error) {
            console.log(error);
        }
    }
    const getVideoSettings = async () => {
        try {
            await axios.get(`https://liveshopping.asia/getSwirlShortCodeInfoByBrandId?brandId=${user.brand_id}`).then((res) => {

                if (res.data.sscResponse) {
                    setVideoSettings(res.data.sscResponse.brandCustomization)
                }

            })
        } catch (error) {
            console.log(error);
        }
    }

    const getVideoData = async () => {
        try {
            await axios.get(`https://liveshopping.asia/getVideoInfoByVideoId?videoId=${videoData.video_id}`).then((res) => {
                if (res.data?.swirl) {
                    setFetchedVideoData(res.data.swirl)

                }
            }).catch((error) => {
                console.log(error);
            })
        } catch (error) {

        }
    }

    useEffect(() => {
        getPlalists()
        getProducts()
        getVideoSettings()
        getVideoData()
    }, [])

    useEffect(() => {
        if (fetchedVideoData) {
            const filteredObjects = playlists.filter(obj => {
                // Get the array of IDs from fetchedVideoData.playlistId
                const playlistIds = fetchedVideoData?.playlistId?.split(',') || [];

                // Check if the current object's ID matches any ID in the playlistIds array
                return playlistIds.includes(obj.id.toString());
            });

            setselectedPlalist(filteredObjects)
            setImagePreview(fetchedVideoData.coverImage)
            setImage(fetchedVideoData.coverImage)
            setvideoFIle(fetchedVideoData.serverUrl)
            setInputFields(prevInputFields => ({
                ...prevInputFields,
                pageUrl: fetchedVideoData.shopifyUrl,
            }));
            if (fetchedVideoData.shopifyUrl) {
                if (fetchedVideoData.shopifyUrl.includes("https") || fetchedVideoData.shopifyUrl.includes("http")) {
                    setSelectedCTATab("1")
                    setInputFields(prevInputFields => ({
                        ...prevInputFields,
                        "pageUrl": fetchedVideoData?.shopifyUrl?.trim(),
                    }));
                } else {
                    setSelectedCTATab("2")

                    if (fetchedVideoData.shopifyUrl.includes("+")) {
                        setSelectedCallNowType("1")
                        // setNumber(fetchedVideoData.shopifyUrl)
                        const arr = fetchedVideoData?.shopifyUrl?.split("+")
                        setNumber(arr[2].trim())
                        // console.log(getCountryCodeByPhoneCode(`+${arr[1].trim()}`));
                        setSelectedCountry((getCountryCodeByPhoneCode(`+${arr[1].trim()}`)))

                    } else {
                        setSelectedCallNowType("0")
                        setNumber(fetchedVideoData?.shopifyUrl?.trim())
                    }
                }
            } else if (fetchedVideoData.productId) {
                // console.log(products);
                setSelectedCTATab("0")
                const filteredObjects = products.filter(obj => {
                    // Get the array of IDs from fetchedVideoData.playlistId
                    const productIds = fetchedVideoData?.productId?.split(',')?.map(id => id.trim()) || [];
                    // Check if the current object's ID matches any ID in the playlistIds array
                    return productIds.includes(obj.productId.toString());
                });
                setSelectedItems(filteredObjects)
            }
            // if()
        }

    }, [playlists, fetchedVideoData])


    useEffect(() => {
        // setInputFields(prevInputFields => ({
        //     ...prevInputFields,
        //     ctaBtn: videoSettings.buy_btn,
        // }));
        setInputFields(prevInputFields => ({
            ...prevInputFields,
            videoTitle: fetchedVideoData.videoTitle,
        }));
        if (fetchedVideoData?.ctaButton) {

            if (!JSON.parse(fetchedVideoData?.ctaButton).cta_bg_color) {
                setInputFields(prevInputFields => ({
                    ...prevInputFields,
                    ctaBtn: videoSettings.buy_btn,
                }));

                setColor(videoSettings.bk_color_buy_btn)
                setColor2(videoSettings.front_color_buy_btn)
            } else {
                setInputFields(prevInputFields => ({
                    ...prevInputFields,
                    ctaBtn: JSON.parse(fetchedVideoData.ctaButton).cta_btn_text,
                }));
                setColor(JSON.parse(fetchedVideoData.ctaButton).cta_bg_color)
                setColor2(JSON.parse(fetchedVideoData.ctaButton).cta_text_color)
            }
        }

    }, [videoSettings, fetchedVideoData])
    useEffect(() => {
        videoDimentionType()
    }, [videoFIle])
    // video length
    // phone num
    // country code
    const fetchDta = async () => {
        const proxyUrl = 'https://cors-anywhere.herokuapp.com/';
        const targetUrl = inputFields.pageUrl;

        try {
            const response = await axios.get(proxyUrl + targetUrl);
            const parser = new DOMParser();
            const doc = parser.parseFromString(response.data, 'text/html');
            const titleTag = doc.getElementsByTagName('title')[0];
            const titleText = titleTag ? titleTag.innerText : 'No title found';
            if (titleText) {
                setInputFields(prevInputFields => ({
                    ...prevInputFields,
                    videoTitle: titleText,
                }));
            }
            // setTitle(titleText);
            // setError(null);
        } catch (err) {
            // setTitle(null);
            // setError('Failed to fetch the title');
            console.log(err);
        }
    }
    useEffect(() => {
        if (inputFields.pageUrl) {
            fetchDta()
        }
    }, [inputFields.pageUrl])

    return (
        <div style={{ overflow: "hidden" }}>
            <div className='row' >
                <div className='col-8' style={{ height: "80vh", overflow: "auto" }}>
                    <p className='d-flex align-items-center gap-2'><BiArrowBack title='back' style={{ cursor: "pointer" }} onClick={() => navigate("/admin/svideos")} size={20} />Edit Video</p>
                    <div className='row text-muted'>
                        <Form onSubmit={(e) => {
                            {

                                handleSubmitEdit(e)
                            }
                        }}>


                            <label className='mt-3'>Title <sup className='text-danger'>*</sup></label>
                            <Form.Control type='text' className='mt-2' placeholder='Enter video title' value={inputFields.videoTitle} name="videoTitle" onChange={(e) => {
                                handleInputChangeAllFields(e)
                            }} required />
                            <div className='d-flex mt-3'>
                                <Form.Check
                                    type="radio"
                                    label="Connect products"
                                    name="connect_product"
                                    id="connect_product"
                                    value="0"
                                    checked={selectedCTATab === '0'}
                                    onChange={handleCTATabChange}
                                />
                                <Form.Check
                                    type="radio"
                                    label="Add URL"
                                    name="add_url"
                                    id="add_url"
                                    value="1"
                                    className='ms-3'
                                    checked={selectedCTATab === '1'}
                                    onChange={handleCTATabChange}
                                />
                                <Form.Check
                                    type="radio"
                                    label="Call now"
                                    name="c_now"
                                    id="c_now"
                                    value="2"
                                    className='ms-3'
                                    checked={selectedCTATab === '2'}
                                    onChange={handleCTATabChange}
                                />
                            </div>
                            <div>
                                {selectedCTATab === '0' && (
                                    <SelecteProducts data={products} selectedItems={selectedItems} setSelectedItems={setSelectedItems} />
                                )}
                                {selectedCTATab === '1' && (
                                    <Form.Control
                                        type="text"
                                        className='form-control mt-3'
                                        placeholder="Add page URL"
                                        name="pageUrl"
                                        value={inputFields.pageUrl}
                                        onChange={(e) => {
                                            handleInputChangeAllFields(e)
                                        }}

                                    // Add your props or state management for female input here
                                    />
                                )}

                                {selectedCTATab === '2' && (
                                    <>
                                        <div className='row mt-3'>
                                            <div className='col-3'>
                                                <Form.Select aria-label="select call now type" className='text-muted' onChange={(e) => {
                                                    setSelectedCallNowType(e.target.value)
                                                }}>
                                                    <option selected={selectedCallNowType === "0"} value="0">Toll-free</option>
                                                    <option selected={selectedCallNowType === "1"} value="1">Phone number</option>

                                                </Form.Select>
                                            </div>
                                            <div className='col-9'>
                                                {selectedCallNowType == "0" ? <Form.Control type='number' value={number} onChange={(e) => setNumber(e.target.value)} placeholder={selectedCallNowType == "0" ? "Enter toll-free number" : "Enter phone number"} aria-label="Text input with dropdown button" /> : <InputGroup className="mb-3 country_btn">
                                                    <DropdownButton
                                                        variant="outline-dark"
                                                        title={selectedCountry}
                                                        className='country_btn'
                                                        onSelect={(e) => handleDropdownSelect(e)}
                                                        style={{ border: "1px solid red" }}

                                                    >
                                                        <Dropdown.Item eventKey="US">US</Dropdown.Item>
                                                        <Dropdown.Item eventKey="IN">IN</Dropdown.Item>
                                                        <Dropdown.Item eventKey="AU">AU</Dropdown.Item>
                                                    </DropdownButton>
                                                    <Form.Control type='number' value={number} placeholder={selectedCallNowType == "0" ? "Enter toll-free number" : "Enter phone number"} aria-label="Text input with dropdown button" onChange={(e) => setNumber(e.target.value)} />
                                                </InputGroup>}

                                            </div>
                                        </div>
                                    </>
                                )}
                                <p className='mt-3'>Overlay CTA button</p>
                                <div className='row mt-2'>
                                    <div className='col-4'>
                                        <input
                                            type="text"
                                            value={inputFields.ctaBtn}
                                            placeholder='Enquire now'
                                            className="form-control"
                                            name='ctaBtn'
                                            onChange={(e) => handleInputChangeAllFields(e)}
                                        />
                                    </div>
                                    <div className='col-4'>

                                        <div className="input-group mb-3">
                                            <span className="input-group-text">
                                                <span className="color_badge" style={{ backgroundColor: color }}></span>
                                            </span>
                                            <input
                                                type="text"
                                                value={color}
                                                onClick={handleClick}
                                                className="form-control pointer_cursor"

                                            />
                                            <span className="input-group-text pointer_cursor" onClick={handleClick}>
                                                <IoColorPaletteOutline />
                                            </span>
                                        </div>

                                        {showColorPicker && (
                                            <div ref={colorPickerRef} style={{ position: 'absolute', zIndex: '2' }}>
                                                <ChromePicker color={color} onChange={handleColorChange} />
                                            </div>
                                        )}
                                    </div>
                                    <div className='col-4'>
                                        <div className="input-group mb-3">
                                            <span className="input-group-text">
                                                <span className="color_badge" style={{ backgroundColor: color2 }}></span>
                                            </span>
                                            <input
                                                type="text"
                                                value={color2}
                                                onClick={handleClick2}
                                                className="form-control pointer_cursor"

                                            />
                                            <span className="input-group-text pointer_cursor" onClick={handleClick2}>
                                                <IoColorPaletteOutline />
                                            </span>
                                        </div>

                                        {showColorPicker2 && (
                                            <div ref={colorPickerRef2} style={{ position: 'absolute', zIndex: '2' }}>
                                                <ChromePicker color={color2} onChange={handleColorChange2} />
                                            </div>
                                        )}
                                    </div>
                                </div>

                            </div>
                            <p>Select playlist</p>
                            <SelectPlayList data={playlists} selectedPlalist={selectedPlalist} setselectedPlalist={setselectedPlalist} />
                            <p className='mt-2'>Cover image</p>
                            <div className="input-group ">
                                <Form.Control ref={fileInputRef} type="file" accept="image/*" placeholder="Select an image file" // Change the placeholder
                                    data-browse="Browse" onChange={handleFileSelect} />
                                {/* <input
                                    type="text"
                                    className="form-control"
                                    placeholder='Select image'
                                /> */}
                                {/* <span className="input-group-text">
                                    <small >Browse</small>
                                </span> */}
                            </div>

                            <small>File format: .png, .jpg, .webp</small>
                            {imagePreview && <>
                                <div className='card p-2 col-4 mt-4 m-auto'>
                                    <img alt='preview' className='rounded' src={imagePreview} />
                                </div>
                            </>}
                            {/* {imagePreview && image && <div className='card p-1 mt-2'>
                                <div className='d-flex w-100'>
                                    <img style={{ height: "50px", width: "35px", borderRadius: "3px" }} src={imagePreview} />
                                    <div className='d-flex flex-column ps-2 pt-1'>
                                        <small className='text-muted'>{fileInputRef?.current?.files[0]?.name}</small>
                                        <small style={{ color: "rgb(162, 162, 164)" }}>{`${(fileInputRef?.current?.files[0]?.size / 1024).toFixed(2)}KB`} <b>.</b> <span className='text-success'>Uploaded</span></small>
                                    </div>
                                    <div className='ms-auto' onClick={() => clearSelection()}>
                                        <IoClose className='pointer_cursor' />
                                    </div>
                                </div>

                            </div>} */}
                            <div>
                                <button className='btn btn-sm mt-3 btn-dark'>Update {loading ? <FaSpinner className="spinning" /> : ""}</button>
                            </div>
                        </Form>

                    </div>
                </div>
                <div className='col-4' >
                    <div className='card' style={{ height: "80vh", padding: "50px", paddingTop: "20px", borderRadius: "20px", overflow: "hidden", backgroundColor: "#F8F8FF", borderRadius: '10px' }}>

                        <div>

                            <div style={{ position: "relative", height: "70vh", maxWidth: "100%", overflow: "hidden", borderRadius: "20px" }}>

                                {videoFIle ? <video ref={videoRef} style={{ position: "absolute", top: 0, left: 0, width: "100%", height: "100%", objectFit: "cover" }} src={videoFIle} autoPlay muted /> : <div className='h-100 w-100' style={{ backgroundColor: "#D9D9D9" }}></div>}

                                {previewUrl && <div style={{ position: "absolute", top: 0, right: 0, padding: "10px", color: "white" }} onClick={clearPreview}>
                                    <span style={{ height: "34px", width: "34px", backgroundColor: "#a2a2a4", cursor: "pointer", borderRadius: "50%", display: "grid", placeItems: "center" }}>
                                        <BiTrash className=' float-end' />
                                    </span>
                                </div>}
                                <div style={{ position: "absolute", top: "50%", left: "50%", transform: "translate(-50%, -50%)", textAlign: "center", color: "white" }}>
                                    <div style={{ backgroundColor: "#ffffff", height: "44px", width: "44px", display: "grid", placeItems: "center", borderRadius: "50%" }}>
                                        <FaPlay size={20} className='ps-1 pointer_cursor' color='#09121F' />
                                    </div>
                                </div>
                                {selectedItems.length > 0 &&
                                    <div className='w-100 p-3' style={{ position: "absolute", bottom: "0" }}>
                                        <div className='d-flex bg-light rounded overflow-hidden' style={{
                                            boxShadow: "0px 4px 4px 0px #00000040"
                                        }}>
                                            {/* <img src='https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcS3CoT43dhDs24KOBENldI_y_OMEpP430XknIzXNGfp2Q&s' style={{ height: "105px", width: "76px" }} />
                                            <div className='d-flex flex-column w-100 ps-2'>
                                                <div className='m-0 p-0' style={{ fontSize: "12px",left:"80%" }}><span className='badge text-muted p-2' style={{ backgroundColor: "#FCFCE4" }}><BsFillStarFill className='text-success' />4.5</span></div>
                                                <span className='product_title_preview' style={{ fontSize: "13px" }}>{selectedItems[0].productTitle.substring(0, 25)}</span>
                                                <span style={{ fontSize: "12px", padding: "2px" }}>$300 <del className="text-muted">$600</del><span className='badge_discount bg-success text-white fw-light p-1 rounded ms-1'><small>30%</small></span></span>
                                                <div className='d-flex align-items-center gap-2'>
                                                    <button className='btn btn-sm btn-dark' style={{ fontSize: "12px", width: "68%" }}>Buy Now</button>
                                                    <MdAddShoppingCart size={35} className='pe-2' />
                                                </div>
                                            </div> */}


                                        </div>
                                    </div>}

                            </div>

                            {/* <div style={{ height: "70vh", maxWidth: "100%", objectFit: "cover", borderRadius: "20px", overflow: "hidden", position: "absolute" }}>
                                <video ref={videoRef} style={{ height: "70vh", objectFit: "cover" }} src={previewUrl} autoPlay width="100%" />
                            </div>
                            <div style={{ position: "relative", height: "70vh", color: "#fff" }}>
                                <div>
                                    <BiTrash className='text-danger float-end' />
                                </div>
                                <div style={{ height: '70vh', display: "grid", placeItems: "center" }}>
                                    <div style={{ backgroundColor: "#ffffff", height: "44px", width: "44px", display: "grid", placeItems: "center", borderRadius: "50%" }}>
                                        <FaPlay size={20} className='ps-1 pointer_cursor' color='#09121F' />
                                    </div>
                                </div>
                            </div>
                            <div className='text-center'>
                                <button className='btn btn-dark mt-2' disabled={previewUrl ? false : true} onClick={() => setPreviewModel(true)}>Preview</button>
                            </div> */}
                            <div className='text-center'>
                                <button className='btn btn-dark mt-2' disabled={imagePreview ? false : true} onClick={() => setPreviewModel(true)}>Preview</button>
                            </div>
                        </div>

                        {/* <RangeSlider min="5" max="20" id="range-slider" /> */}
                    </div>

                </div>
                <VideoPreviewModel show={previewModel} url={videoFIle} handleClose={handleClosePreviewModel} />
            </div>
            {/* {loading && <div style={{ position: "relative", height: "80vh", backgroundColor: "rgba(255,255,255,0.6)", width: "100%", display: "grid", placeItems: "center" }}>
                <FallingLines
                    color="#EEEE77"
                    width="100"
                    visible={true}
                    ariaLabel="falling-circles-loading"
                />
            </div>} */}
        </div>
    )
}

export default VideoEditPage