import React, { useState } from 'react';
import Select from 'react-select';
import { MdDragIndicator } from "react-icons/md";
import { IoClose } from "react-icons/io5";
import { toast } from 'react-toastify';

const SelecteProducts = ({ data, selectedItems, setSelectedItems }) => {

    const handleItemSelect = (selectedOption) => {
        setSelectedItems(selectedOption);
    };

    const handleDragStart = (e, index) => {
        e.dataTransfer.setData('index', index);
    };

    const handleDrop = (e, index) => {
        const draggedIndex = e.dataTransfer.getData('index');
        const draggedItem = selectedItems[draggedIndex];

        const newItems = [...selectedItems];
        newItems.splice(draggedIndex, 1);
        newItems.splice(index, 0, draggedItem);

        setSelectedItems(newItems);
    };

    const handleDragOver = (e) => {
        e.preventDefault();
    };

    const handleInputChange = (index, value) => {
        const newItems = [...selectedItems];
        newItems[index].productTitle = value;
        setSelectedItems(newItems);
    };

    const handleRemoveItem = (index) => {
        const newItems = [...selectedItems];
        newItems.splice(index, 1);
        setSelectedItems(newItems);
    };
    const customStyles = {
        control: (provided) => ({
            ...provided,
            width: '100%', // Make the input field full-width
        }),
        option: (provided) => ({
            ...provided,
            fontSize: '12px', // Adjust the font size for smaller tags
        }),
        // singleValue: (provided) => ({
        //     ...provided,
        //     fontSize: '12px', // Adjust the font size for smaller tags
        //     width: '300px'
        // }),
    };
    const truncateLabel = (label, maxLength) => {
        if (label?.length <= maxLength) {
            return label;
        }
        return label?.substring(0, maxLength) + '...';
    };

    const customFormatTagsLabel = ({ productTitle }, type) => {

        if (!productTitle) return
        if (type === "option") {
            return productTitle
        } else {
            return truncateLabel(productTitle, 40);
        }

    };


    return (
        <div>

            <div className='mt-3'>
                <Select
                    options={data}
                    // formatOptionLabel={(e) => customFormatTagsLabel(e, "tag")}
                    formatSelectedOptionLabel={(e) => customFormatTagsLabel(e, 'option')}
                    hideSelectedOptions={true}
                    className='w-100'
                    isMulti={true}
                    value={selectedItems}
                    getOptionLabel={(option) => option.productTitle}
                    getOptionValue={(option) => option.productId}
                    onChange={handleItemSelect}
                    placeholder="Attach Products"
                    closeMenuOnSelect={false}
                    styles={customStyles}
                    backspaceRemovesValue
                    isOptionDisabled={() => selectedItems.length === 7}

                />
            </div>
            <div style={{ display: "flex", flexWrap: 'wrap' }}>
                {selectedItems.map((item, index) => (
                    <div
                        key={index}
                        onDragStart={(e) => handleDragStart(e, index)}
                        onDragOver={handleDragOver}
                        onDrop={(e) => handleDrop(e, index)}
                        draggable
                        className='m-1'
                        style={{ background: "#EEEE77", color: "#17171C", padding: "3px 9px", borderRadius: '15px', cursor: "pointer" }}
                    >
                        <small title={item.productTitle}> <MdDragIndicator /> {item.productTitle.slice(0, 20)} <IoClose style={{ marginTop: "-2px" }} onClick={() => handleRemoveItem(index)} /></small>
                    </div>
                ))}
            </div>
        </div>
    );
};

export default SelecteProducts;
