import React from 'react'
import { Modal } from 'react-bootstrap';
const VideoPreviewModel = ({ show, handleClose, url }) => {
    return (
        <Modal show={show} className='d-grid modal_video' size='lg' onHide={handleClose}>

            <Modal.Body>
                <div className='row m-0 p-0' >
                    <div style={{ maxWidth: "400px", margin: "0", padding: "0" }}>
                        <video className='modal_video' controls width="600" height="400" autoPlay loop>
                            <source src={url} type="video/mp4" />
                            Your browser does not support the video tag.
                        </video>
                    </div>

                </div>
            </Modal.Body>

        </Modal>
    )
}

export default VideoPreviewModel