import React, { useState } from 'react';
import Select, { components } from 'react-select';
import { MdDragIndicator } from "react-icons/md";
import { IoClose } from "react-icons/io5";

const SelectPlayList = ({ selectedPlalist, data, setselectedPlalist }) => {
    const handleItemSelect = (selectedOption) => {
        if (selectedOption.length > 0) {

            if (parseInt(selectedOption[selectedOption.length - 1].videoCount) > 9) {
                // setselectedPlalist(selectedOption.slice(0, selectedOption.length - 1));
            } else {
                setselectedPlalist(selectedOption)
            }
        } else {
            setselectedPlalist(selectedOption)
        }

    };

    const handleDragStart = (e, index) => {
        e.dataTransfer.setData('index', index);
    };

    const handleDrop = (e, index) => {
        const draggedIndex = e.dataTransfer.getData('index');
        const draggedItem = selectedPlalist[draggedIndex];

        const newItems = [...selectedPlalist];
        newItems.splice(draggedIndex, 1);
        newItems.splice(index, 0, draggedItem);

        setselectedPlalist(newItems);
    };

    const handleDragOver = (e) => {
        e.preventDefault();
    };

    const handleInputChange = (index, value) => {
        const newItems = [...selectedPlalist];
        newItems[index].name = value;
        setselectedPlalist(newItems);
    };

    const handleRemoveItem = (index) => {
        const newItems = [...selectedPlalist];
        newItems.splice(index, 1);
        setselectedPlalist(newItems);
    };

    const CustomOption = ({ children, ...props }) => {
        return (
            <components.Option {...props} isDisabled={parseInt(props.data.videoCount) > 9 ? true : false}>
                <div className="w-100 d-flex">
                    <span>{children} {" "}{parseInt(props.data.videoCount) > 9 ? "(Max videos selected)" : ""}</span>
                    <span
                        className="text-dark float-end ms-auto"
                        style={{
                            fontSize: "14px",
                            paddingTop: "3px",
                            paddingLeft: "3px",
                            paddingRight: "3px",
                            borderRadius: "15px",
                            backgroundColor: "rgb(238, 238, 119)",
                        }}
                    >
                        <small>
                            {props.data.videoCount}{" "}
                            {parseInt(props.data.videoCount) > 1 ? "Videos" : "Video"}
                        </small>
                    </span>
                </div>

            </components.Option>
        );
    }
    return (
        <div>

            <div>
                <Select
                    options={data}
                    isMulti
                    value={selectedPlalist}
                    onChange={handleItemSelect}
                    placeholder="Select playlist"
                    getOptionLabel={(option) => option.name}
                    getOptionValue={(option) => option.id}
                    closeMenuOnSelect={false}
                    components={{ Option: CustomOption }}
                // components={{ DropdownIndicator: () => null, IndicatorSeparator: () => null }}
                />
            </div>
            <div style={{ display: "flex" }}>
                {selectedPlalist.map((item, index) => (
                    <div
                        key={index}
                        onDragStart={(e) => handleDragStart(e, index)}
                        onDragOver={handleDragOver}
                        onDrop={(e) => handleDrop(e, index)}
                        draggable
                        className='m-1'
                        style={{ background: "#EEEE77", color: "#17171C", padding: "3px 9px", borderRadius: '15px', cursor: "pointer" }}
                    >
                        <small> <MdDragIndicator /> {item.name} <IoClose style={{ marginTop: "-2px" }} onClick={() => handleRemoveItem(index)} /></small>
                    </div>
                ))}
            </div>
        </div >
    );
};

export default SelectPlayList;
